<template>
  <div class="container">
    <div v-if="allProducts.isLoading">
      Loading...
    </div>
    <div v-else>
      <div class="row">
        <div v-for="(item, index) in allProducts" :key="index">
          <div class="card-img">
          <b-button type="submit" variant="inline" @click="$emit('onadd', item.id)">
            <img :src="`http://18.205.153.196:3000/`+item.gambar" alt="">
          </b-button>
          </div>
          <div class="card-item">
            <p>{{item.nama_produk}} </p>
            <p><b>Rp.  {{item.harga}}</b></p>
          </div>
          <button type="button" class="btn btn-sm ml-1 pull-right del-icon" data-toggle="modal" @click="DeleteProduct(item.id)">
            <b-icon icon="trash" class="rounded-circle bg-danger p-2" variant="light" style="width: 40px; height: 40px;"></b-icon>
          </button>
          <button class="btn btn-sm ml-1" v-b-modal.modal-update @click="updateProduk(item.id, index)">
            <b-icon icon="pencil-square" class="rounded-circle bg-info p-2" variant="light" style="width: 40px; height: 40px;"></b-icon>
          </button>
        </div>
        <b-modal id="modal-update" title="BoostrapVue" hide-header hide-footer>
          <div>
          <form @submit="updateProduk" enctype="multipart/form-data">
            <div class="row">
              <div class="col-sm-10">
                <h4 class="text-left font-weight-bold mt-1 mb-4">Edit Data</h4>
              </div>
            </div>
            <div class="form-grup row">
              <label for="gambar" class="col-sm-2 col-form-label"><strong>Image</strong></label>
              <div class="col-sm-10">
                <input style="width: 274px; margin-bottom: 15px; margin-left: 2.0rem !important;" type="file" @change="processFile($event)" class="form-control form-control-lg form-additem pl-2" id="gambar" />
              </div>
            </div>
            <div class="form-group row">
              <label for="nama_produk" class="col-sm-2 col-form-label"><strong>Name</strong></label>
              <div class="form-sm-10">
                <input type="text" class="form-control form-control-lg form-additem ml-5" id="nama_produk" v-model="nameProduct" />
              </div>
            </div>
            <div class="form-group row">
              <label for="harga" class="col-sm-2 col-form-label"><strong>Price</strong></label>
              <div class="form-sm-10">
                <input type="text" class="form-control form-control-lg form-additem ml-5" id="harga" v-model="price" />
              </div>
            </div>
            <div class="form-group row">
              <label for="id_category" class="col-sm-2 col-form-label"><strong>id_category</strong></label>
              <div class="form-sm-10 row ml-5">
                <select class="form-control form-control-lg form-additem" v-model="idCategory" id="id_category">
                  <option :value='17'>Coffe</option>
                  <option :value='16'>Food</option>
                </select>
              </div>
            </div>
            <div class="form-button">
              <input class="mt-3 btn-block btn-info cancel" type="submit" value="Send" @click.prevent="sendData"/>
              <input class="mt-3 btn-block btn-danger cancel" type="button" value="cancel" @click="$emit('addclose')"/>
            </div>
          </form>
          </div>
        </b-modal>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import env from '../helpers/env'
import Swal from 'sweetalert2'

export default {
  name: 'Card',
  helpers: [env],
  data () {
    return {
      newInput: {
        data: null,
        detail: {},
        id: null,
        namaProduk: null,
        idCategory: null,
        harga: null,
        image: null,
        selecting: false
      },
      input: {
        dataCategory: null,
        oldid: null,
        oldnamaProduk: null,
        oldidCategory: null,
        oldharga: null,
        oldgambar: null
      },
      idProduk: null,
      gambar: null,
      nameProduct: null,
      price: null,
      idCategory: null
    }
  },
  computed: {
    ...mapGetters({
      allProducts: 'products/getAllProducts',
      allCategory: 'products/allCategory'
    })
  },
  methods: {
    ...mapActions({
      actionGetAllProducts: 'products/getAllProducts',
      actionDeleteDatas: 'products/deleteData',
      getCategory: 'products/getCategory',
      updateData: 'products/updateData'
    }),
    hideModal () {
      this.$refs['my-modal'].hide()
    },
    toggleModal () {
      this.$refs['my-modal'].toggle('#toggle-btn')
    },
    detail (id) {
      this.$router.push(`/detail/${id}`)
    },
    onAdd () {
      this.$emit('emitAdd')
    },
    processFile (event) {
      this.gambar = event.target.files[0]
    },
    updateProduk (id, index) {
      this.idProduk = this.allProducts[index].id
      this.gambar = this.allProducts[index].gambar
      this.nameProduct = this.allProducts[index].nama_produk
      this.price = this.allProducts[index].harga
      this.idCategory = this.allProducts[index].id_category
    },
    sendData () {
      const fd = new FormData()
      fd.append('gambar', this.gambar)
      fd.append('nama_produk', this.nameProduct)
      fd.append('harga', this.price)
      fd.append('id_category', this.idCategory)
      const data = {
        idProduk: this.idProduk,
        form: fd
      }
      this.updateData(data)
        .then((response) => {
          if (response === 'File size max 500 KB') {
            Swal.fire({
              icon: 'error',
              title: 'File to large! MAX 500 KB'
            })
          } else if (response === 'Image type must png or jpg') {
            Swal.fire({
              icon: 'error',
              title: 'Image type must png or jpg'
            })
          } else {
            Swal.fire({
              icon: 'success',
              title: 'Insert New Data Success'
            })
            location.reload()
          }
        }).catch((err) => {
          alert(err)
        })
    },
    update (namaProduk, idCategory, harga, gambar) {
      this.newInput.namaProduk = namaProduk
      this.newInput.idCategory = idCategory
      this.newInput.harga = harga
      this.newInput.gambar = gambar
      this.input.oldnamaProduk = this.newInput.namaProduk
      this.input.oldidCategory = this.newInput.idCategory
      this.input.oldHarga = this.newInput.harga
      this.input.oldGambar = this.newInput.gambar
    },
    DeleteProduct (id) {
      const conf = confirm('Do you want to delete this item ?')
      if (conf) {
        this.actionDeleteDatas(id).then((response) => {
          window.location = '/'
        }).catch((err) => {
          alert(err)
        })
      }
    }
  },
  mounted () {
    // this.allProducts()
    this.actionGetAllProducts()
  },
  created () {
    this.getCategory()
  }
}
</script>

<style scoped>
