<template>
  <div class="menu-item">
      <!-- MENU -->
      <a href>
        <b-button variant="inline-warning">
          <router-link to="/"><img src="../assets/img/fork.png" alt /></router-link>
        </b-button>
      </a>
      <a href>
        <b-button variant="inline-warning">
          <router-link to="/History"><img src="../assets/img/clipboard.png" alt /></router-link>
        </b-button>
      </a>
      <!-- MODAL ADD-->
      <b-button v-b-modal.modal-lg variant="inline">
        <img src="../assets/img/add.png" alt />
      </b-button>
      <b-modal id="modal-lg" size="lg" title="Add Item" hide-header hide-footer>
          <h4 style="text-align: left;">
          <b>Add Item</b>
          </h4>
          <form @submit.prevent="insertData" enctype="multipart/form-data">
            <div class="m-2 m-2 form-group row font-weight-bold">
              <label>File</label>
              <input type="file" @change="processFile($event)" class="form-control form-control-lg form-additem"/>
            </div>
            <div class="m-2 form-group row font-weight-bold">
              <label>Name</label>
              <input class="col-form-label col-form-label-lg" type="text" v-model="input.nama_produk"/>
            </div>
            <div class="m-2 form-group row font-weight-bold">
              <label>Price</label>
              <input type="text" class="form-control form-control-lg form-additem" v-model="input.harga"/>
            </div>
            <div class="m-2 form-group row font-weight-bold">
              <label for="category">id category</label>
              <select class="form-control form-control-lg form-additem" v-model="input.id_category" id="category" required>
                <option selected :value='null'>Category</option>
                <option :value='17'>Coffe</option>
                <option :value='16'>Food</option>
              </select>
            </div>
            <span></span>
            <div class="form-button">
              <b-button class="mt-3 btn-block btn-info" type="submit" block @click="loadOnce" name="button">Add</b-button>
              <b-button class="mt-3 btn-block btn-danger" value="cancel" type="button" @click="$emit('addclose')" name="button">Cancel</b-button>
            </div>
          </form>
         </b-modal>
      <!-- END MODAL ADD -->
      <button class="btn btn-inline btn-xl" @click="onLogout()"><img src="../assets/logout.png" alt=""></button>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Swal from 'sweetalert2'

export default {
  name: 'Menu',
  data () {
    return {
      input: {
        gambar: null,
        nama_produk: null,
        harga: null,
        id_category: null
      }
    }
  },
  computed: {
    ...mapGetters({
      allCategory: 'products/allCategory'
    })
  },
  methods: {
    ...mapActions({
      insertData: 'products/sendData',
      getCategory: 'products/getCategory',
      actionLogout: 'auth/logout'
    }),
    onLogout () {
      this.actionLogout().then((responses) => {
        window.location = '/login'
      })
    },
    loadOnce () {
      this.gambar = this.input.gambar
      this.insertData(this.input)
        .then((response) => {
          console.log(response.data.message)
          if (response.data.message === 'File size max 500 KB') {
            Swal.fire({
              icon: 'error',
              title: 'File to large! MAX 500 KB'
            })
          } else if (response.data.message === 'Image type must png or jpg') {
            Swal.fire({
              icon: 'error',
              title: 'Image type must png or jpg'
            })
          } else {
            Swal.fire({
              icon: 'success',
              title: 'Insert New Data Success'
            })
            location.reload()
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    processFile (event) {
      this.input.gambar = event.target.files[0]
      // console.log(this.image)
    }
  }
}
</script>

<style scoped>
</style>
