<template>
  <div>
    <div class="navbar-main navbar navbar-default">
      <a href>
        <img src="../assets/img/menu1.png" alt />
      </a>
      <p>
        <b>Food Items</b>
      </p>
      <p>
      <b-input-group size="sm" class="mb-2 search-item">
        <b-form-input  type="search"  v-model="search" placeholder="Search"></b-form-input>
        <b-input-group-prepend is-text>
          <b-icon icon="search" class="cari" @click="searchProducts()"></b-icon>
        </b-input-group-prepend>
      </b-input-group>
      </p>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'Navbar',
  data () {
    return {
      search: null
    }
  },
  methods: {
    ...mapActions({
      actionSearch: 'products/searchProduct'
    }),
    searchProducts () {
      this.$router.push({ path: '/', query: { search: this.search } })
      this.actionSearch(this.search)
    }
  }
}
</script>
