<template>
  <div id="app">
    <div id="nav">
      <div class="container-fluit">
        <div class="row">
          <div class="col-lg-8 nopadding">
            <Navbar @search="searchProducts"/>
            <div class="row">
              <div class="col-lg-1 nopadding menu-box">
                <div class="menu">
                  <Menu/>
                </div>
              </div>
              <div class="col-lg-11 nopadding">
                <div class="content">
                  <div class="search-sort">
                    <b-form-select v-model="sort" @change="sortingProduct" class="form-control form-additem">
                      <template v-slot:first>
                        <b-form-select-option :value="null" disabled>Sort by</b-form-select-option>
                      </template>
                      <b-form-select-option :value="{sort: 'nama_produk', type: 'asc'}">Name A - Z</b-form-select-option>
                      <b-form-select-option :value="{sort: 'nama_produk', type: 'desc'}">Name Z - A</b-form-select-option>
                      <b-form-select-option :value="{sort: 'harga', type: 'asc'}">Cheap</b-form-select-option>
                      <b-form-select-option :value="{sort: 'harga', type: 'desc'}">Expensive</b-form-select-option>
                    </b-form-select>
                  </div>
                  <div class="row card-item">
                    <Card @onadd='onAdd'/>
                  </div>
                  <div class="col-12 no-gutters mt-3 pagenation">
                    <div class="" @click="page()">
                      <b-pagination v-model="currentPage" :per-page="limit" :total-rows="rows"></b-pagination>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-4 nopadding cart-item">
            <Cart/>
            <div class="col-md-12">
              <div v-if="cart.length ===0 " class="text-center">
                <Orders1/>
              </div>
              <div v-else>
                <div v-for="(item, index) in cart" :key="index">
                  <div class="cart-list">
                    <div class="d-flex">
                      <div class="p-2">
                        <img :src="'http://18.205.153.196:3000/'+item.gambar" alt="" />
                      </div>
                      <div class="p-2 align-self-start">
                        <p class="name-list"><b>{{item.nama_produk}}</b></p>
                        <div class="plus-minus btn-group mr-2" role="group" aria-label="Second group">
                          <button type="button" class="minus btn" @click="minus(index)">-</button>
                          <button type="button" class="number btn">{{item.qty}}</button>
                          <button type="button" class="plus btn" @click="plus(index)">+</button>
                        </div>
                      </div>
                      <div class="p-2">
                        <p class="price"><b>Rp. {{item.harga*item.qty}}</b></p>
                      </div>
                    </div>
                  </div>
                </div>
                  <div class="main-counting-cart">
                    <div class="cart-counting">
                        <h3><b>Total :</b></h3>
                        <h3><b>Rp. {{totalPrice()}}</b></h3>
                    </div>
                    <p>*Belum termasuk ppn</p>
                    <div class="main-btn-cart">
                        <button @click="checkout(item)" v-b-modal.modal-co>Checkout</button>
                        <button>Cancel</button>
                    </div>
                  </div>
              </div>
            </div>
          </div>
          <b-modal id="modal-co" hide-footer title="Modal Checkout">
          <b-col lg="12">
              <b-row>
                  <b-col lg="12">
                      <h4>IINVOICE #2456</h4>
                  </b-col>
                <b-col lg="12" class="pt-3">
                    <b-row>
                          <b-col lg="6" cols="6" class="mt-2 pb-4 name-item">Cashier :</b-col>
                  <b-col lg="6" cols="6">
                      <input type="text" class="form-control" placeholder="cashier name..">
                  </b-col>
                    </b-row>
                </b-col>
                <b-col lg="12" class="mt-3 item-getmenu" v-for="(item,index) in cart" :key="index">
                    <b-row>
                        <b-col lg="5" cols="5">
                            <p class="name-item">{{item.nama_produk}}</p>
                        </b-col>
                        <b-col lg="2" cols="2">
                            {{item.qty}}
                        </b-col>
                        <b-col lg="5" cols="5">
                            <p class="price-item">Rp. {{item.qty * item.harga}}</p>
                        </b-col>
                    </b-row>
                </b-col>
                <b-col lg="12" class="mt-5">
                    <b-row>
                        <b-col lg="6" cols="6" class="text-left">
                            <h4 class="name-item">Total : </h4>
                        </b-col>
                        <b-col lg="6" cols="6">
                            <h4 class="price-item">Rp. {{totalPrice()}}</h4>
                        </b-col>
                    </b-row>
                </b-col>
                <b-col lg="12">
                    <p>*PPN 10%</p>
                </b-col>
                <b-col lg="12" class="my-3">
                    <button @click="print" class="btn btn-primary">PRINT</button>
                </b-col>
              </b-row>
          </b-col>
        </b-modal>
        </div>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
import Navbar from '../components/Navbar'
import Menu from '../components/Menu'
import Cart from '../components/Cart'
import Card from '../components/Card'
import Orders1 from '../components/Orders1'
import { mapState, mapGetters, mapActions } from 'vuex'
import JsPDF from 'jspdf'
import Swal from 'sweetalert2'

export default {
  data () {
    return {
      users: [],
      product: [],
      cart: [],
      inputTest: '',
      sort: null,
      type: '',
      rows: null,
      limit: 9,
      currentPage: 1,
      priceget: [],
      sumCart: null
    }
  },
  computed: {
    ...mapState(['test']),
    ...mapState({
      testing: 'test',
      getAllProduct: 'product'
    }),
    ...mapGetters({
      getter2: 'getterTest',
      allProducts: 'products/getAllProducts'
    })
  },
  components: {
    Navbar,
    Menu,
    Cart,
    Card,
    Orders1
  },
  methods: {
    ...mapActions({
      onClickTest: 'onClickTes',
      actionGetAllProducts: 'products/getAllProducts',
      actionDeleteData: 'products/deleteData',
      actionSearch: 'products/searchProduct',
      sortProduct: 'products/sortProduct',
      actionPage: 'products/pagination'
    }),
    searchProducts () {
      // console.log(this.search)
      this.$router.push({ path: '/', query: { search: this.search } })
      this.actionSearch(this.search)
    },
    sortingProduct () {
      this.$router.push({ path: '/', query: { sort: this.sort.sort, type: this.sort.type } })
      this.sortProduct(this.sort)
    },
    page () {
      const payload = {
        // limit: this.limit,
        page: this.currentPage
      }
      this.$router.push({ path: '/', query: { page: this.currentPage } })
      this.actionPage(payload)
        .then((response) => {
          this.rows = response.meta.totalRow
        })
        .catch((err) => {
          console.log(err)
        })
    },
    submitForm () {
      this.onClickTest(this.inputTest)
    },
    onAdd (id, index) {
      const cart = this.cart.filter((e) => e.id === id)
      if (cart.length === 0) {
        const dataCart = this.allProducts.filter((e) => e.id === id)
        dataCart[0].qty = 1
        this.cart = [
          ...this.cart, dataCart[0]
        ]
        this.priceget = [
          ...this.priceget, dataCart[0].harga
        ]
        this.sumCart = this.priceget
      } else {
        const oldCart = this.cart.map((e) => {
          if (e.id === id) {
            e.qty += 1
          }
          return e
        })
        this.cart = oldCart
      }
    },
    totalPrice () {
      let total = 0
      for (let i = 0; i < this.cart.length; i++) {
        total += this.cart[i].harga * this.cart[i].qty
      }
      return total
    },
    minus (index) {
      const cart1 = this.cart[index].id
      const dataFilter = this.cart.filter((e) => {
        if (e.id === cart1) {
          e.qty -= 1
          if (e.qty < 1) {
            e.qty = 1
          }
        }
        return e
      })
      this.cart = dataFilter
    },
    plus (index) {
      const cart1 = this.cart[index].id
      const dataFilter = this.cart.filter((e) => {
        if (e.id === cart1) {
          e.qty += 1
        }
        return e
      })
      this.cart = dataFilter
    },
    checkout (item) {
      localStorage.setItem('cart', this.item)
    },
    print () {
      const doc = new JsPDF()
      doc.setFontSize(12)
      doc.text('Checkout', 10, 10)
      doc.text(`Date : ${new Date().toJSON().slice(0, 10)}`, 160, 10)
      doc.text('Cashier : Pevita Pearce', 20, 40)
      doc.text('Receipt no : #2456', 140, 40)
      doc.text('Order : ', 20, 50)
      const checkout = []
      for (var i in this.cart) {
        checkout.push(`${this.cart[i].nama_produk} ${this.cart[i].qty}x (@Rp. ${this.cart[i].harga})`)
      }
      const qtyCheckout = []
      for (var j in this.cart) {
        qtyCheckout.push(`Rp. ${this.cart[j].harga * this.cart[j].qty}`)
      }
      const dist = checkout.length * 5
      doc.text(checkout, 37, 50)
      doc.text(qtyCheckout, 140, 50)
      doc.text('PPN (10%) : ', 20, 60 + dist)
      doc.text(`Rp. ${this.totalPrice() * 0.1}`, 140, 60 + dist)
      doc.text('Total Price :  ', 20, 70 + dist)
      doc.text(`Rp. ${this.totalPrice() + (this.totalPrice() * 0.1)}`, 140, 70 + dist)
      doc.text('Thanks for orders See you!', 76, 80 + dist)
      doc.save('Checkout.pdf')
      Swal.fire(
        'Thank You!',
        'Downloaded!',
        'success'
      )
    }
  },
  mounted () {
    this.actionGetAllProducts()
    console.log(this.actionGetAllProducts)
    this.actionPage({ limit: '', page: '' })
      .then((response) => {
        this.rows = response.meta.totalRow
      })
      .catch((err) => {
        console.log(err)
      })
    this.users = this.allProducts
  }
}
</script>

<style scoped>
  .search-sort{
    position: absolute;
    width: 150px;
  }
  .card-item{
    padding-top: 36px;
  }
  .pagenation{
    position: static;
    padding-top: 20px;
  }
  .name-item {
    text-align: left;
  }
  .price-item {
    text-align: right;
  }
</style>
