import axios from 'axios'
import { IP_ADDRESS } from '../../helpers/env'

const state = () => {
  return {
    all: {
      products: [],
      isLoading: false
    },
    pagination: null
  }
}
const getters = {
  getAllProducts (state) {
    return state.all.products
  }
}
const mutations = {
  SET_ALL_PRODUCTS (state, payload) {
    state.all.products = payload
  },
  SET_ALL_CATEGORY (state, payload) {
    state.all.category = payload
  },
  SET_ALL_DETAIL_PRODUCT (state, payload) {
    state.all.getDetail = payload
  },
  SET_ALL_LOADING (state, payload) {
    state.all.isLoading = payload
  },
  SET_PAGINATION (state, payload) {
    state.pagination = payload
  },
  SET_SEARCH (state, payload) {
    state.all.products = payload
  },
  SET_SORT (state, payload) {
    state.all.products = payload
  }
}
const actions = {
  getAllProducts (context, payload) {
    context.commit('SET_ALL_LOADING', true)
    return new Promise((resolve, reject) => {
      axios.get(`${IP_ADDRESS}/product/getall`).then((response) => {
        // console.log(response)
        context.commit('SET_ALL_PRODUCTS', response.data.data)
        resolve(response.data.data)
      }).catch((error) => {
        // console.log(error)
        reject(error)
      }).finally(() => {
        context.commit('SET_ALL_LOADING', false)
      })
    })
  },
  pagination (context, payload) {
    context.commit('SET_ALL_LOADING', true)
    const fd = {
      limit: payload.limit === undefined ? '' : payload.limit,
      page: payload.page === undefined ? '' : payload.page
    }
    return new Promise((resolve, reject) => {
      axios.get(`${IP_ADDRESS}/product/getall?limit=${fd.limit}&page=${fd.page}`).then((response) => {
        // console.log(response.data.meta)
        context.commit('SET_ALL_PRODUCTS', response.data.data)
        resolve(response.data)
      }).catch((error) => {
        // console.log(error)
        reject(error)
      }).finally(() => {
        context.commit('SET_ALL_LOADING', false)
      })
    })
  },
  async searchProduct (context, payload) {
    try {
      const result = await axios.get(`${IP_ADDRESS}/product/getall?search=${payload}`)
      context.commit('SET_SEARCH', result.data.data, payload)
    } catch (error) {
      console.log(error.message)
    }
  },
  async sortProduct (context, payload) {
    console.log(payload)
    try {
      const result = await axios.get(`${IP_ADDRESS}/product/getall?sort=${payload.sort}&type=${payload.type}`)
      context.commit('SET_SORT', result.data.data, payload)
    } catch (error) {
      console.log(error.message)
    }
  },
  updateData (context, payload) {
    context.commit('SET_ALL_LOADING', true)
    return new Promise((resolve, reject) => {
      axios.patch(`${IP_ADDRESS}/product/update/${payload.idProduk}`, payload.form).then((response) => {
        // console.log(response)
        // context.commit('SET_ALL_PRODUCTS', response.data.data)
        resolve(response.data.message)
      }).catch((error) => {
        reject(error)
      }).finally(() => {
        context.commit('SET_ALL_LOADING', false)
      })
    })
  },
  deleteData (context, payload) {
    return new Promise((resolve, reject) => {
      axios.delete(`${IP_ADDRESS}/product/delete/${payload}`, {
      }).then((response) => {
        resolve(response.data.message)
      }).catch((err) => {
        reject(err)
      })
    })
  },
  sendData (context, payload) {
    return new Promise((resolve, reject) => {
      const fd = new FormData()
      fd.append('gambar', payload.gambar)
      fd.append('nama_produk', payload.nama_produk)
      fd.append('harga', payload.harga)
      fd.append('id_category', payload.id_category)
      axios.post(`${IP_ADDRESS}/product/insert`, fd)
        .then((response) => {
          // console.log(response)
          resolve(response)
        })
        .catch((err) => {
          reject(err)
        })
    })
  },
  update (context, payload) {
    return new Promise((resolve, reject) => {
      const fd = new FormData()
      fd.append('gambar', payload.gambar)
      fd.append('nama_produk', payload.oldnamaProduk)
      fd.append('harga', payload.oldHarga)
      fd.append('id_category', payload.oldidCategory)
      axios.patch(`${IP_ADDRESS}/product/update`, fd)
        .then((response) => {
          resolve(response)
        })
        .catch((err) => {
          reject(err)
        })
    })
  },
  getCategory (context, payload) {
    return new Promise((resolve, reject) => {
      axios.get(`${IP_ADDRESS}/category/getall`)
        .then(response => {
          context.commit('SET_ALL_CATEGORY', response.data.data)
          resolve()
        })
        .catch(err => {
          reject(err)
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
