<template>
  <div class="container-fluid" style="padding-right: 0px; padding-left: 0px; overflow-x: hidden">
    <div class="row">
      <div class="col-md-8" style="padding-right: 0px; padding-left: 0px;">
        <img class="bg" src="../assets/img/bag-coffe.jpg" alt="">
      </div>
      <div class="card col-md-4" style="padding-right: 0px; padding-left: 0px;">
        <h1 class="pt-5">Login</h1>
        <div class="form-login">
          <form class="m-5" v-on:submit.prevent="onLogin">
            <input type="email" v-model="form.email" class="m-2 form-control" placeholder="email" required>
            <input type="password" v-model="form.password" class="m-2 form-control" placeholder="password" required>
            <div class="">
              <button type="submit" class="btn btn-success">Login</button>
            </div>
            <div class="pt-3">
              <p>Dont't have account yet ?</p>
              <router-link to="/Register">Register Here..!</router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { mapActions } from 'vuex'
import Swal from 'sweetalert2'

export default {
  data () {
    return {
      form: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    onLogin () {
      this.actionLogin(this.form).then((response) => {
        console.log(response)
        if (response === 'Wrong password/email') {
          Swal.fire({
            icon: 'error',
            title: 'Wrong Password'
          })
        } else if (response === "Cannot read property 'password' of undefined") {
          Swal.fire({
            icon: 'error',
            title: 'Wrong Email'
          })
        } else {
          window.location = '/'
        }
      }).catch((err) => {
        alert(err)
      })
    },
    ...mapActions({
      actionLogin: 'auth/login'
    })
  }
}
</script>

<style scoped>
.form-login {
  width: 100%;
}
.bg {
  width: 100%;
  height: 700px;
}
@media (max-width: 992px) {
  .bg {
    display: none;
  }
}
</style>
