<template>
  <div class="container-fluid" style="padding-right: 0px; padding-left: 0px; overflow-x: hidden">
    <div class="row">
      <div class="col-md-8" style="padding-right: 0px; padding-left: 0px;">
        <img class="bg" src="../assets/img/bag-coffe.jpg" alt="">
      </div>
      <div class="card col-md-4" style="padding-right: 0px; padding-left: 0px;">
        <h1 class="pt-5">Register</h1>
        <div class="form-register">
          <form class="m-5" v-on:submit.prevent="onRegister">
            <input type="email" v-model="form.email" class="m-2 form-control" placeholder="email" required>
            <input type="password" v-model="form.password" class="m-2 form-control" placeholder="password" required>
            <div class="">
              <button type="submit" class="btn btn-danger">Register</button>
            </div>
            <div class="pt-3">
              <router-link to="/login"><p>Login here..!</p></router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { mapActions } from 'vuex'
import Swal from 'sweetalert2'

export default {
  data () {
    return {
      form: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    onRegister () {
      this.actionRegister(this.form).then((response) => {
        console.log(response)
        if (response === 'User Already exist') {
          Swal.fire({
            icon: 'error',
            title: 'User Already exist'
          })
        } else if (response === 'Please check your mail for activation') {
          Swal.fire({
            icon: 'success',
            title: 'Please check your mail for activation'
          })
        }
      }).catch((err) => {
        alert(err)
      })
    },
    ...mapActions({
      actionRegister: 'auth/register'
    })
  }
}
</script>

<style scoped>
.form-register {
  width: 100%;
}
.bg {
  width: 100%;
  height: 700px;
}
@media (max-width: 992px) {
  .bg {
    display: none;
  }
}
</style>
